.header {
  height: 100px;
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: space-between;
  background-color: #46add6;
  padding: 0 20px;
}
.header__img {
  width: 50px;
  height: 50px;
}
.header__img > img {
  width: 100%;
  height: 100%;
}
.header__title {
  font-size: 2.5rem;
  color: #fff;
  font-family: cursive;
}
