.todoItem {
  display: flex;
  align-items: center;
  width: 90%;
  animation-name: show;
  animation-duration: 1s;
  height: 40px;
  border: 1px dashed #5778c0;
  margin-bottom: 20px;
}
.todoItem__pic {
  width: 30px;
  height: 30px;
  opacity: 0;
  transition: 0.5s ease all;
}
.todoItem__pic > img {
  width: 100%;
  height: 100%;
}
@keyframes show {
  from {
    margin-top: -20px;
    opacity: 0;
  }
  to {
    margin: 0;
    opacity: 1;
  }
}
.on {
  opacity: 1;
}
